.interaction .screen-left .showWrap {
  width: 100%;
  padding: 33px 25px 20px 30px;
  height: calc(100vh - 100px);
  position: relative;
  background: #1E1D23;
}
.interaction .screen-left .showWrap > div {
  width: 100%;
  height: 100%;
}
.interaction .screen-left .showWrap > div .preview-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}
.interaction .screen-left .showWrap > div .preview-wrapper .preview_img {
  height: 100%;
}
.interaction .screen-left .showWrap > div .preview-wrapper #previewIframe {
  width: 100%;
  height: 100%;
  background: #111111;
  position: relative;
}
.interaction .screen-left .showWrap > div .preview-wrapper #previewIframe .none-scroll {
  background: #E6E6E6;
  position: absolute;
  right: 0;
  height: 95.4%;
  width: 3.2%;
  top: 4.6%;
}
.interaction .screen-left .showWrap > div .preview-wrapper #previewIframe iframe {
  width: 100%;
  height: 100%;
}
.interaction .screen-left .showWrap > div .preview-wrapper #previewIframe .prev-btn {
  position: absolute;
  width: 40px;
  height: 35px;
  color: #ffffff;
  font-size: 17px;
  bottom: 0;
  text-align: center;
  line-height: 33px;
  left: 42%;
  background: #d24726;
  z-index: 20;
  cursor: pointer;
}
.interaction .screen-left .showWrap > div .preview-wrapper #previewIframe .next-btn {
  position: absolute;
  width: 40px;
  height: 35px;
  color: #ffffff;
  font-size: 17px;
  bottom: 0;
  text-align: center;
  line-height: 33px;
  left: 55%;
  background: #d24726;
  z-index: 20;
  cursor: pointer;
}
.interaction .screen-left .showWrap > div .preview-wrapper #previewIframe .next-pdf-btn {
  position: absolute;
  background: #4e4e4e;
  width: 39px;
  height: 36px;
  line-height: 36px;
  text-align: center;
  color: #ffffff;
  top: 0;
  right: 0%;
  font-size: 22px;
  z-index: 20;
  cursor: pointer;
}
.interaction .screen-left .showWrap > div .preview-wrapper #previewIframe .prev-pdf-btn {
  position: absolute;
  background: #4e4e4e;
  width: 39px;
  height: 36px;
  line-height: 36px;
  text-align: center;
  color: #ffffff;
  top: 0;
  right: 3%;
  font-size: 22px;
  z-index: 20;
  cursor: pointer;
}
.interaction .resource-bottom img {
  width: 50px;
  height: 50px;
  margin-right: 20px;
}
.interaction .resource-bottom .ant-col {
  display: flex;
  background: #dfdfdf;
  padding: 10px;
  border-radius: 5px;
  margin: 10px 0;
}
.preview_ideo {
  padding-top: 44px;
  background: black;
  outline: none;
  border-radius: 7px;
}
.pdf-canvas {
  text-align: center;
}
.pdf-canvas canvas {
  height: 800px;
}
.previewIframe {
  margin: 0 auto;
  height: 800px;
}
.previewIframe iframe {
  height: 100%;
}
.pg-viewer-wrapper {
  overflow: hidden !important;
}
.pg-viewer > div > div {
  max-height: 760px;
  overflow: auto;
}
.border_none .ant-modal-confirm-btns .ant-btn:nth-child(1):hover {
  border-color: #d9d9d9;
  color: rgba(0, 0, 0, 0.65);
}
.border_none .ant-modal-confirm-btns .ant-btn-primary {
  border-color: white !important;
}
.delete_activity .ant-modal-body {
  width: 400px;
  height: 260px;
}
.delete_activity .ant-modal-body .ant-modal-confirm-body {
  margin-top: 21px;
}
.delete_activity .ant-modal-body .ant-modal-confirm-body .ant-modal-confirm-content {
  margin-left: 2px;
  margin-top: 21px;
}
.delete_activity .ant-modal-body .ant-modal-confirm-btns {
  position: absolute;
  bottom: 11px;
  right: 30px;
}
.delete_activity .ant-modal-body .ant-modal-confirm-btns .ant-btn:nth-child(1):hover {
  border-color: #d9d9d9;
  color: rgba(0, 0, 0, 0.65);
}
.delete_activity .ant-modal-body .ant-modal-confirm-btns .ant-btn-primary {
  border-color: white !important;
}
.preview-img-box {
  height: 750px;
  display: flex;
  justify-content: center;
}
.preview-img-box .img-box {
  text-align: center;
  height: 100%;
}
.preview-img-box .img-box img {
  height: 100%;
}
.preview-img-box > span {
  margin-left: 15px;
  font-size: 16px;
  display: flex;
  flex-direction: column-reverse;
}
.audio_content {
  width: 100%;
  height: 100%;
}
.audio_content .audio_wrapper {
  width: 100%;
  height: 90%;
  background: #111111;
  border-radius: 7px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  color: #888888;
}
.audio_content .audio_wrapper > div {
  text-align: center;
}
.audio_content .audio_wrapper > div > div:nth-child(1) {
  margin-bottom: 30px;
}
.audio_content .audio_wrapper > div > div:nth-child(1) img {
  width: 53px;
  height: 60px;
}
.audio_content .audio_footer {
  width: 100%;
  height: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.audio_content .audio_footer audio {
  width: 75%;
  height: 30px;
}
.page-btn {
  position: absolute;
  bottom: 20px;
  right: 5%;
  transform: translate(-50%, 0px);
}
.page-btn .ant-btn {
  margin: 0px 10px;
}
.page-btn .ant-tag {
  padding: 2px 10px;
  margin: 0px 10px;
  cursor: pointer;
}
